<template>
<div :class="loading ? 'loader-main' : ''">
    <nav v-if="!loading" class="navbar navbar-expand-lg navbar-light bg-light">
      <a class="navbar-brand" href="#">AntGate</a>
      <button class="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarNav" aria-controls="navbarNav" aria-expanded="false" aria-label="Toggle navigation">
        <span class="navbar-toggler-icon"></span>
      </button>
      <div class="collapse navbar-collapse" id="navbarNav">
        <ul class="navbar-nav">
          <li class="nav-item active">
            <a class="nav-link" href="#/">Home <span class="sr-only">(current)</span></a>
          </li>
          <li class="nav-item active">
            <a class="nav-link" href="#/addCategory">Add Category <span class="sr-only">(current)</span></a>
          </li>
          <li class="nav-item active">
            <a class="nav-link" href="#/category">Category <span class="sr-only">(current)</span></a>
          </li>
          <li class="nav-item active">
                <a class="nav-link" href="#/books">Books <span class="sr-only">(current)</span></a>
            </li>
        </ul>
      </div>
    </nav>

    <div v-if="!loading">
        <form>
            <!-- Book Title -->
            <div class="col-auto mt-5">
            <label class="sr-only" for="bookTitle">TITLE</label>
            <div class="input-group mb-2">
                <div class="input-group-prepend">
                <div class="input-group-text">TITLE</div>
                </div>
                <input v-model="title" type="text" class="form-control" id="bookTitle" placeholder="Write Book Title">
            </div>
            </div>

            <!-- Book Body -->
            <div class="col-auto mt-5">
            <label class="sr-only" for="bookBody">BODY</label>
            <div class="input-group mb-2">
                <div class="input-group-prepend">
                <div class="input-group-text">BODY</div>
                </div>
                <textarea v-model="body" type="text" class="form-control" id="bookBody" placeholder="Write Book Body"></textarea>
            </div>
            </div>
        </form>

        <!-- Category Select -->
        <div class="ml-3 mr-3 mt-5">
            <multiselect v-model="value" :options="options" :multiple="true" :close-on-select="false" :clear-on-select="false" :preserve-search="true" placeholder="Pick some" label="name" track-by="name" :preselect-first="true">
            <template slot="selection" slot-scope="{ values , isOpen }"><span class="multiselect__single" v-if="values.length &amp;&amp; !isOpen">{{ values.length }} options selected</span></template>
            </multiselect>
        </div>

        <!-- Button -->
        <button type="button" class="btn btn-primary mt-5 w-50 btn-upload" @click="upload">Upload</button>
    </div>

    <!-- Spinner -->
    <SpinnerLoading :loading="loading"></SpinnerLoading>
</div>
</template>

<script>
import Multiselect from 'vue-multiselect'
import axios from 'axios'
import SpinnerLoading from './SpinnerLoading.vue'

export default {
  components: {
    Multiselect,
    SpinnerLoading,
  },
  data () {
    return {
        title : null,
        body : null,
        value: [],
        options: [],
        loading : true,
    }
  },
  async created() {
    // Fetch Category From Server
    this.loading = true;
    const response = await axios.get(`${this.$hostname}/getCategory`);
    this.options = response["data"]["data"];
    this.loading = false;
  },
  methods : {
    async upload() {
        this.loading = true;
        //Upload to a Server Books
        const uploadData = {
          "title" : this.title,
          "body" : this.body,
          "category" : this.value.map(cat=> cat['value']),
        }

        await axios.post(`${this.$hostname}/books`,uploadData);
        this.loading = false;
        this.$router.push('/books');
    }
  }
}
</script>

